<template>
    <div class="h">
        <div class="mBg h">
            <mhead-top titleHead="编辑购物车" lfIcon="el-icon-arrow-left icon" lfUrl="back" rgIcon="el-icon-finished icon" rgUrl="/cart/cart"></mhead-top>
            <div class="mbg">
                <div class="unLogin" v-if="!login">
                    <img src="">
                    <el-button class="lBtn" type="primary">登录</el-button>
                </div>
                <div v-else>
                    <div v-if="list.length!=0">
                        <div class="m-c-item" v-for="(item,index) in list" :key="index">
                            <div class="m-c-check">
                                <el-checkbox v-model="item.checked" name="cartBox" @change="handleCheckedChange()"></el-checkbox>
                            </div>
                            <div class="m-c-pic">
                                <el-image class="mc-image" :src="item.pic"></el-image>
                            </div>
                            <div class="m-c-info">
                                <div class="m-c-name">{{item.name}}</div>
                                <div class="m-c-spec">
                                    <span>{{item.spec_name}}</span>
                                </div>
                                <div class="m-c-price">
                                    <div class="mc-price">￥{{item.price}}</div>
                                    <div class="mc-number">
                                        <el-input-number @change="numberChange(item.number,item.cart_id,true,index)" size="mini" v-model="item.number"></el-input-number>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="countNav">
                            <div class="countCheck">
                                <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                            </div>
                            <div class="m-editInfo">
                                <el-button size="mini" icon="el-icon-star-off" plain type="warning" round>移入收藏夹</el-button>
                                <el-button @click="delCartGoods()" size="mini" icon="el-icon-delete" plain type="danger" round>删除所选</el-button>
                            </div>
                        </div>
                    </div>
                    <div v-else>
                        <p class="f16 tc mt30">购物车暂无商品</p>
                    </div>
                </div>
            </div>
        </div>
        <nav-foot menuActive="cart"></nav-foot>
    </div>
</template>


<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import navFoot from '../../components/mobile/navFoot';
    import mheadTop from '../../components/mobile/headTop';
    import {cartList,delCartGoods,changeCartGoodsnum} from '../../service/api'

    export default {
        name: 'mCartEdit',
        data(){
            return{
                checkAll: false,
                isIndeterminate: true,//全选样式
                list:[],
            }
        },
        components:{
            navFoot,
            mheadTop
        },
        mounted(){
            this.getUserInfo();
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','login'
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            async initData(){
                if (this.userInfo){
                    let res = await cartList();
                    this.list = res.cartInfo;
                    for(var i = 0 ; i < this.list.length ; i++){
                        this.list[i].checked = false;
                    }
                }
            },
            handleCheckAllChange(val) {
                if(val){
                    //val = true
                    for(var i = 0 ; i < this.list.length ; i++){
                        this.list[i].checked = true;
                    }
                }else{
                    //val = false
                    for(var i = 0 ; i < this.list.length ; i++){
                        this.list[i].checked = false;
                    }
                }
                this.isIndeterminate = false;
            },
            handleCheckedChange() {
                var checkArr = document.getElementsByName("cartBox");
                var checked_counts = 0;
                for(var i = 0 ; i < checkArr.length; i++){
                    if(checkArr[i].checked){
                        checked_counts++;
                    }
                }
                this.isIndeterminate = checked_counts==checkArr.length? false : true;
                this.checkAll = checked_counts==checkArr.length? true : false;
            },
            async editCart(number,cartId,isCheck){
                let res = await changeCartGoodsnum(number,cartId,isCheck);
            },
            numberChange(number,cartId,isCheck,index){
                this.editCart(number,cartId,isCheck);
                this.list[index].checked = true;
            },
            delCartGoods(cart_id){
                this.$confirm('确认删除该产品?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'info'
                }).then(async () => {
                    for(var i = 0 ;  i < this.list.length ; i++){
                        if(this.list[i].checked){
                            let res = await delCartGoods(this.list[i].cart_id);
                        }
                    }
                    let newList = await cartList();
                    if(newList.length > 0){
                        this.list = newList.cartInfo;
                        for(var i=0 ; i < this.list.length ; i++){
                            this.list[i].checked = false;
                        }
                    }else{
                        this.$router.push({ path: '/cart/cart' });
                    }
                }).catch(() => {
                });
            }

        },
        created(){

        },
        watch:{
           
        }

    }

</script>

<style lang="scss">
    @import '../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .mbg{
        padding-bottom: px2rem(96);
    }
    /*未登录*/
    .unLogin{
        width: 100%;
        text-align:center;
        padding-top:px2rem(200);
    }
    .lBtn{
        width:px2rem(400);
        font-size:px2rem(32);
    }
    /*展示*/
    .m-c-item{
        background: #fff;
        padding:px2rem(20);
        margin-top:px2rem(20);
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
    }
    .m-c-check{
        width:px2rem(50);
        height: px2rem(180);
        line-height: px2rem(180);
    }
    .m-c-pic{
        width:px2rem(180);
        height: px2rem(180);
        padding-right:px2rem(20);
    }
    .mc-image{
        width:px2rem(180);
        height: px2rem(180);
        object-fit: contain;
        background-color: #f2f2f2;
    }
    .m-c-info{
        width:px2rem(460);
    }
    .m-c-name{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        color:#333;
        line-height: px2rem(36);
        min-width: px2rem(460);
    }
    .m-c-spec{
        font-size:px2rem(22);
        color:#999;
        display: inline-block;
        border-right: 3px;
        flex-wrap: nowrap;
        padding:0 px2rem(10);
        height: px2rem(36);
        line-height: px2rem(36);
        margin-top:5px;
    }
    .m-c-spec span{
        display: block;
        float:left;
    }
    .m-c-spec .icon{
        font-size:px2rem(22);
        color:#999;
        height: px2rem(36);
        line-height: px2rem(36);
        padding-left:px2rem(6);
        float:left;
    }
    .m-c-price{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .mc-price{
        text-align: left;
    }

    /*下单Nav*/
    .countNav{
        display: flex;
        flex-wrap:nowrap;
        height: px2rem(96);
        line-height: px2rem(96);
        background-color: #fff;
        position: fixed;
        left:0;
        bottom:px2rem(104);
        width: px2rem(750);
    }
    .countCheck{
        width:px2rem(200);
        padding-left: px2rem(20);
    }
    .m-editInfo{
        width:px2rem(530);
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-end;
        padding:px2rem(10) px2rem(20);
        text-align: right;

    }





</style>



